/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

fieldset {
  border: 2px solid #007bff;
  border-radius: 10px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

main {
  max-width: 600px;
  margin: 4rem auto;
  background-color: #ffffff;
  padding: 2rem 3rem;
  border-radius: 15px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.campo input[type=text],
.campo input[type=tel],
.campo input[type=number] {
  width: 300px;
  height: 40px;
  font-size: 16px;
  padding: 8px;
  flex: 1;
}

table {
  width: 100%;
  border-collapse: collapse;
}

thead {
  background-color: #007bff;
  color: white;
}

th, td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid #ccc;
}

tbody tr:hover {
  background-color: #f1f1f1;
}

.ingreso {
  color: green;
  font-weight: bold;
}

.egreso {
  color: red;
  font-weight: bold;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

/* Estilos base para todos los botones */
.boton {
  background-color: #FFC107;
  color: #333;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-top: 4rem;
  /* Efecto hover (animaci√≥n) */
  /* Efecto al hacer clic */
}
.boton:hover {
  background-color: #FFD54F;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}
.boton:active {
  transform: translateY(0);
}

.volver {
  display: inline-block;
  margin-top: 2rem;
  background-color: #007bff;
  color: white;
  padding: 0.8rem 1.5rem;
  text-decoration: none;
  border-radius: 8px;
}

.boton-rojo-block {
  background-color: red;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: black;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: rgb(229.5, 0, 0);
  cursor: pointer;
}

/* Estilos para los botones de acciones */
.acciones {
  display: flex;
  gap: 10px; /* Espacio entre botones */
  margin-top: 15px;
}

.boton-eliminar {
  background-color: #ff4d4d;
  color: white;
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
  text-decoration: none;
  display: inline-block;
  text-align: center;
}

.boton-eliminar:hover {
  background-color: #ff1a1a;
}

.boton-actualizar {
  background-color: #4CAF50;
  color: white;
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
  text-decoration: none;
  display: inline-block;
  text-align: center;
}

.boton-actualizar:hover {
  background-color: #3e8e41;
}

/* Para el formulario inline del bot√≥n eliminar */
.acciones form {
  margin: 0; /* Elimina m√°rgenes por defecto */
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.search-input {
  padding: 3rem 1.5rem; /* M√°s alto */
  font-size: 1.2rem; /* Texto m√°s grande */
  border: 1px solid #ccc;
  border-radius: 2rem;
  width: 400px; /* M√°s ancho */
  transition: all 0.3s ease;
}

.search-input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 5px rgba(52, 152, 219, 0.5);
}

.search-button {
  background-color: #3498db; /* Azul de fondo */
  border: none;
  padding: 12px; /* Tama√±o adecuado para hacer el bot√≥n redondo */
  cursor: pointer;
  border-radius: 50%; /* Bot√≥n redondeado */
  transition: background-color 0.3s, transform 0.3s;
}

.search-button:hover {
  background-color: #2980b9; /* Azul m√°s oscuro cuando pasa el mouse */
  transform: scale(1.1); /* Aumenta ligeramente el tama√±o */
}

.search-button i {
  pointer-events: none;
}

.icono-lupa {
  width: 20px;
  height: 20px;
  fill: white; /* √çcono blanco para que contraste con el azul */
  transition: fill 0.3s, transform 0.3s; /* Transici√≥n suave en el color y el tama√±o */
}

.search-button:hover .icono-lupa {
  fill: #f0f0f0; /* √çcono m√°s claro cuando pasa el mouse */
  transform: rotate(360deg); /* Gira la lupa */
}

.search-button:hover .icono-lupa {
  fill: #FF7B1D; /* Color cuando pasa el mouse */
  transform: rotate(360deg); /* Gira la lupa */
}

body {
  background-color: #e8f5e9;
  margin: 0;
  font-family: Arial, sans-serif;
}

.header {
  background-color: white;
  border-radius: 0 0 15px 15px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  padding: 1rem 0;
  position: relative;
  z-index: 1000;
}

.login-link a {
  text-decoration: none;
}
.login-link a h3 {
  margin: 0;
  font-weight: 700;
  font-size: 1.2rem;
  color: #007bff;
  transition: color 0.3s ease;
}
.login-link a h3:hover {
  color: #0056b3;
}

/* Estilos para el footer */
.footer {
  background-color: #000;
  padding: 2rem 0;
  margin-top: 3rem;
  color: #fff;
  text-align: center;
  /* Estilos para la navegaci√≥n */
  /* Texto adicional (opcional) */
}
.footer .contenedor-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer .navegacion {
  display: flex;
  gap: 2rem;
  justify-content: center;
  margin-bottom: 1rem;
}
.footer .navegacion a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}
.footer .navegacion a:hover {
  color: #FFC107;
  text-decoration: underline;
}
.footer .copyright {
  font-size: 0.9rem;
  opacity: 0.8;
  color: #fff;
}

body {
  background-color: #e8f5e9;
  margin: 0;
  font-family: Arial, sans-serif;
}

.grid-productos {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 20px;
}

.producto {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  padding: 15px;
  text-align: center;
}

.producto img {
  width: 100%;
  border-radius: 6px;
  margin-bottom: 10px;
}

.producto h3 {
  margin: 10px 0 5px;
}

.total {
  margin-top: 20px;
  font-weight: bold;
}

.boton {
  margin: 10px 10px 0 0;
  padding: 10px 15px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.boton:hover {
  background-color: #45a049;
}

.login-container {
  max-width: 400px;
  margin: 5rem auto;
  padding: 2rem;
  background-color: #f5f5f5;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.login-container h2 {
  text-align: center;
  margin-bottom: 2rem;
}

.formulario label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.formulario input[type=text],
.formulario input[type=password] {
  width: 100%;
  padding: 0.8rem;
  margin-bottom: 1.5rem;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.formulario input[type=submit] {
  background-color: #007bff;
  color: white;
  padding: 0.8rem;
  border: none;
  width: 100%;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.formulario input[type=submit]:hover {
  background-color: #0056b3;
}

.barra {
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: space-between;
}
.barra img {
  height: 60px;
}
.barra .inicio {
  font-family: "Lato", sans-serif;
  font-size: 3rem;
  font-weight: 900;
  color: #2c3e50;
  margin: 0;
}

@media (max-width: 600px) {
  .barra {
    flex-direction: column;
    align-items: flex-start;
  }
}
/* Estilos para el formulario */
.formulario {
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
}

.formulario fieldset {
  border: 1px solid #e1e1e1;
  padding: 2rem;
  margin-bottom: 2rem;
}

.formulario legend {
  font-weight: bold;
  padding: 0 1rem;
}

.formulario label {
  display: block;
  margin: 1rem 0;
}

.formulario input {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
}

.boton {
  display: inline-block;
  padding: 0.5rem 1rem;
  text-decoration: none;
  font-weight: bold;
  margin-top: 1rem;
}

.boton-verde {
  background-color: #4CAF50;
  color: white;
}

.alerta.error {
  background-color: #ff4d4d;
  color: white;
  padding: 1rem;
  margin: 1rem 0;
}

/**PAGINAS INTERNAS **/
.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: red;
}
.alerta.exito {
  background-color: #71B100;
}
.alerta .fade-out {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}