.alerta {
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: bold;
    text-transform: uppercase;
    margin: 1rem 0;
    
    &.error {
        background-color: $rojo;
    }

    &.exito {
        background-color: $verde;
    }
    
    .fade-out {
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
    }
}